"use client";

import React from "react";
import { useWizard } from "./WizardContext";

const deployPlatforms = [
  { id: "vercel", label: "Vercel", description: "Deploy to Vercel with automatic CI/CD" },
  { id: "firebase", label: "Firebase", description: "Deploy to Firebase Hosting with Google Cloud" },
];

export default function Step3Deploy() {
  const { deployPlatform, setDeployPlatform, template, modules } = useWizard();

  return (
    <div className="space-y-6">
      <div className="bg-gray-50 p-4 rounded-lg">
        <h3 className="font-medium mb-2">Project Summary</h3>
        <p className="text-sm text-gray-600">Template: {template}</p>
        <p className="text-sm text-gray-600">Modules: {modules.join(", ")}</p>
      </div>
      
      <div className="space-y-3">
        <h3 className="font-medium">Choose Deployment Platform</h3>
        {deployPlatforms.map(({ id, label, description }) => (
          <button
            key={id}
            type="button"
            onClick={() => setDeployPlatform(id as any)}
            className={`w-full border rounded-lg p-4 text-left hover:shadow-md transition ${
              deployPlatform === id ? "border-[#FF5C5C] bg-[#FFF5F5]" : "border-gray-300"
            }`}
          >
            <div className="font-medium">{label}</div>
            <div className="text-sm text-gray-600 mt-1">{description}</div>
          </button>
        ))}
      </div>
    </div>
  );
}
