"use client";

import React from "react";

type ModuleKey = "chatbot" | "content" | "code";

export default function Step2Modules({
  value,
  onChange,
}: {
  value: ModuleKey[];
  onChange: (mods: ModuleKey[]) => void;
}) {
  const modules: { id: ModuleKey; label: string }[] = [
    { id: "chatbot", label: "Chatbot" },
    { id: "content", label: "Content Generator" },
    { id: "code", label: "Code Helper" },
  ];

  const toggleModule = (module: ModuleKey) => {
    if (value.includes(module)) {
      onChange(value.filter((m) => m !== module));
    } else {
      onChange([...value, module]);
    }
  };

  return (
    <div className="grid grid-cols-1 gap-4">
      {modules.map(({ id, label }) => {
        const isSelected = value.includes(id);
        return (
          <button
            key={id}
            type="button"
            onClick={() => toggleModule(id)}
            className={`border rounded-lg p-4 text-center hover:shadow-md transition flex items-center justify-center gap-2 ${
              isSelected ? "border-[#FF5C5C] bg-[#FFF5F5]" : "border-gray-300"
            }`}
          >
            <input
              type="checkbox"
              checked={isSelected}
              readOnly
              className="pointer-events-none"
            />
            <span>{label}</span>
          </button>
        );
      })}
    </div>
  );
}
