"use client";

import React, { createContext, useContext, useState, ReactNode } from "react";

type TemplateKey = "saas" | "ecommerce" | "blog" | "custom";
type ModuleKey = "chatbot" | "content" | "code";
type PlatformKey = "vercel" | "firebase";

interface WizardState {
  currentStep: number;
  template: TemplateKey | null;
  modules: ModuleKey[];
  deployPlatform: PlatformKey | null;
  setCurrentStep: (step: number) => void;
  setTemplate: (template: TemplateKey | null) => void;
  setModules: (modules: ModuleKey[]) => void;
  setDeployPlatform: (platform: PlatformKey | null) => void;
  reset: () => void;
}

const WizardContext = createContext<WizardState | undefined>(undefined);

export function WizardProvider({ children }: { children: ReactNode }) {
  const [currentStep, setCurrentStep] = useState(1);
  const [template, setTemplate] = useState<TemplateKey | null>(null);
  const [modules, setModules] = useState<ModuleKey[]>([]);
  const [deployPlatform, setDeployPlatform] = useState<PlatformKey | null>(null);

  const reset = () => {
    setCurrentStep(1);
    setTemplate(null);
    setModules([]);
    setDeployPlatform(null);
  };

  return (
    <WizardContext.Provider
      value={{
        currentStep,
        template,
        modules,
        deployPlatform,
        setCurrentStep,
        setTemplate,
        setModules,
        setDeployPlatform,
        reset,
      }}
    >
      {children}
    </WizardContext.Provider>
  );
}

export function useWizard() {
  const context = useContext(WizardContext);
  if (!context) {
    throw new Error("useWizard must be used within a WizardProvider");
  }
  return context;
}
