"use client";

import React, { useState } from "react";
import { useWizard } from "./WizardContext";
import Step1Template from "./Step1Template";
import Step2Modules from "./Step2Modules";
import Step3Deploy from "./Step3Deploy";
import { Button } from "@/components/ui/button";
import { useAuth } from "@/lib/auth";
import { createProject } from "@/lib/projectService";
import { Loader2 } from "lucide-react";

type TemplateKey = "saas" | "ecommerce" | "blog" | "custom";
type ModuleKey = "chatbot" | "content" | "code";
type PlatformKey = "vercel" | "firebase";

interface Props {
  onClose: () => void;
}

export default function NewAIProjectWizard({ onClose }: Props) {
  const {
    currentStep,
    setCurrentStep,
    template,
    setTemplate,
    modules,
    setModules,
    deployPlatform,
    setDeployPlatform,
    reset,
  } = useWizard();
  const { user } = useAuth();
  const [isCreating, setIsCreating] = useState(false);

  const isLastStep = currentStep === 3;

  const handleNext = async () => {
    if (isLastStep) {
      if (!user) {
        alert("Please sign in to create a project");
        return;
      }

      setIsCreating(true);
      try {
        // Create project using the project service
        const result = await createProject({
          name: `${template} Project`,
          template: template as TemplateKey,
          modules: modules as ModuleKey[],
          deployPlatform: deployPlatform as PlatformKey,
          description: `AI-powered ${template} project with ${modules.join(", ")} modules`,
        });

        if (result.success && result.projectId) {
          alert(`Project created successfully! Redirecting to project page...`);
          reset();
          onClose();
          // Redirect to project page
          window.location.href = `/projects/${result.projectId}`;
        } else {
          throw new Error(result.error || "Failed to create project");
        }
      } catch (error) {
        console.error("Error creating project:", error);
        alert("Failed to create project. Please try again.");
      } finally {
        setIsCreating(false);
      }
    } else {
      setCurrentStep(currentStep + 1);
    }
  };

  const handleBack = () => {
    if (currentStep > 1) {
      setCurrentStep(currentStep - 1);
    }
  };

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50 p-4">
      <div className="bg-white rounded-lg max-w-lg w-full p-6 shadow-lg">
        <h2 className="text-xl font-semibold mb-4">
          New AI Project - Step {currentStep} of 3
        </h2>
        <div className="mb-6">
          {currentStep === 1 && (
            <Step1Template value={template ?? null} onChange={setTemplate} />
          )}
          {currentStep === 2 && (
            <Step2Modules value={modules} onChange={setModules} />
          )}
          {currentStep === 3 && (
            <Step3Deploy
              template={template!}
              modules={modules}
              value={deployPlatform ?? null}
              onChange={setDeployPlatform}
              onCreate={handleNext}
            />
          )}
        </div>
        <div className="flex justify-between">
          <Button
            variant="outline"
            onClick={handleBack}
            disabled={currentStep === 1 || isCreating}
          >
            Back
          </Button>
          <div className="flex gap-2">
            <Button
              variant="ghost"
              onClick={() => {
                reset();
                onClose();
              }}
              disabled={isCreating}
            >
              Cancel
            </Button>
            <Button
              onClick={handleNext}
              disabled={
                isCreating ||
                (currentStep === 1 && !template) ||
                (currentStep === 2 && modules.length === 0) ||
                (currentStep === 3 && !deployPlatform)
              }
            >
              {isCreating ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Creating...
                </>
              ) : currentStep === 3 ? (
                "Create Project"
              ) : (
                "Next"
              )}
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}
