type TemplateKey = "saas" | "ecommerce" | "blog" | "custom";

export default function Step1Template({
  value,
  onChange,
}: {
  value: TemplateKey | null;
  onChange: (t: TemplateKey) => void;
}) {
  const templates = [
    { id: "saas", label: "SaaS Dashboard" },
    { id: "ecommerce", label: "E-commerce" },
    { id: "blog", label: "Blog" },
    { id: "custom", label: "Custom" },
  ];

  return (
    <div className="grid grid-cols-2 gap-4">
      {templates.map(({ id, label }) => (
        <button
          key={id}
          type="button"
          onClick={() => onChange(id)}
          className={`border rounded-lg p-4 text-center hover:shadow-md transition ${
            value === id ? "border-[#FF5C5C] bg-[#FFF5F5]" : "border-gray-300"
          }`}
        >
          {label}
        </button>
      ))}
    </div>
  );
}
